.text-gray-300{
	--tw-text-opacity: 1;
	color: rgba(209, 213, 219, var(--tw-text-opacity));
}
.opacity-100{
	opacity: 1;
}
.opacity-0{
	opacity: 0;
}
.shadow-md{
	--tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
	box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.shadow-paper{
	--tw-shadow: inset 0px 6px rgb(211, 211, 211), 0px 7px hsl(230, 56%, 48%);
	box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.shadow-rock{
	--tw-shadow: inset 0px 6px rgb(211, 211, 211), 0px 7px hsl(349, 66%, 44%);
	box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.shadow-scissors{
	--tw-shadow: inset 0px 6px rgb(211, 211, 211), 0px 7px hsl(39, 86%, 42%);
	box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.filter{
	-webkit-filter: var(--tw-filter);
	        filter: var(--tw-filter);
}
.transition{
	transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, -webkit-transform, -webkit-filter, -webkit-backdrop-filter;
	transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
	transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-transform, -webkit-filter, -webkit-backdrop-filter;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	transition-duration: 150ms;
}
.delay-500{
	transition-delay: 500ms;
}
.duration-300{
	transition-duration: 300ms;
}
.ease-in-out{
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.paper--winner {
  box-shadow: inset 0px 6px rgb(211, 211, 211), 0px 8px hsl(230, 56%, 48%),
    0 0 0 50px rgba(255, 255, 255, 0.05), 0 0 0 100px rgba(255, 255, 255, 0.03),
    0 0 0 150px rgba(255, 255, 255, 0.02);
}

.rock--winner {
  box-shadow: inset 0px 6px rgb(211, 211, 211), 0px 8px hsl(349, 66%, 44%),
    0 0 0 50px rgba(255, 255, 255, 0.05), 0 0 0 100px rgba(255, 255, 255, 0.03),
    0 0 0 150px rgba(255, 255, 255, 0.02);
}

.scissors--winner {
  box-shadow: inset 0px 6px rgb(211, 211, 211), 0px 8px hsl(39, 86%, 42%),
    0 0 0 50px rgba(255, 255, 255, 0.05), 0 0 0 100px rgba(255, 255, 255, 0.03),
    0 0 0 150px rgba(255, 255, 255, 0.02);
}

.hover\:scale-110:hover{
	--tw-scale-x: 1.1;
	--tw-scale-y: 1.1;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}

.hover\:bg-white:hover{
	--tw-bg-opacity: 1;
	background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
}

.hover\:bg-blue-800:hover{
	--tw-bg-opacity: 1;
	background-color: rgba(30, 64, 175, var(--tw-bg-opacity));
}

.hover\:text-blue-900:hover{
	--tw-text-opacity: 1;
	color: rgba(30, 58, 138, var(--tw-text-opacity));
}

.hover\:text-white:hover{
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.hover\:text-rock:hover{
	--tw-text-opacity: 1;
	color: hsla(349, 71%, 52%, var(--tw-text-opacity));
}

.hover\:text-gray-500:hover{
	--tw-text-opacity: 1;
	color: rgba(107, 114, 128, var(--tw-text-opacity));
}

@media (max-width: 639px){
	.sm\:mt-4{
		margin-top: 1rem;
	}
	.sm\:mb-3{
		margin-bottom: 0.75rem;
	}
	.sm\:h-10{
		height: 2.5rem;
	}
	.sm\:h-24{
		height: 6rem;
	}
	.sm\:w-10{
		width: 2.5rem;
	}
	.sm\:w-24{
		width: 6rem;
	}
	.sm\:-translate-x-3{
		--tw-translate-x: -0.75rem;
		-webkit-transform: var(--tw-transform);
		        transform: var(--tw-transform);
	}
	.sm\:translate-y-44{
		--tw-translate-y: 11rem;
		-webkit-transform: var(--tw-transform);
		        transform: var(--tw-transform);
	}
	.sm\:translate-x-3{
		--tw-translate-x: 0.75rem;
		-webkit-transform: var(--tw-transform);
		        transform: var(--tw-transform);
	}
	.sm\:flex-col{
		flex-direction: column;
	}
	.sm\:flex-col-reverse{
		flex-direction: column-reverse;
	}
	.sm\:gap-8{
		gap: 2rem;
	}
	.sm\:p-3{
		padding: 0.75rem;
	}
	.sm\:pt-3{
		padding-top: 0.75rem;
	}
	.sm\:text-base{
		font-size: 1rem;
		line-height: 1.5rem;
	}
	.sm\:text-3xl{
		font-size: 1.875rem;
		line-height: 2.25rem;
	}
	.sm\:text-4xl{
		font-size: 2.25rem;
		line-height: 2.5rem;
	}
	.sm\:text-2xl{
		font-size: 1.5rem;
		line-height: 2rem;
	}
	.sm\:leading-5{
		line-height: 1.25rem;
	}
}

